@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap);

html{
  scroll-behavior: smooth;
}

body{
  font-family: 'New Tegomin', serif !important;
  background: url(/static/media/bg2.0e2ef8c1.jpg) repeat;
}
:root{
    --primary-white:#fff;
    --primary-red: rgb(116, 38, 38);
    --primary-dark:rgba(225,225,225,0);
  }
  
  /********************** MENU LINKS  ***************************/
  
  nav{
    z-index:2;
  }
  nav a.nav-link{
    font-weight:500;
    letter-spacing:.0375rem;
    cursor:pointer;
  }
  
  nav ul li {
    text-transform:uppercase
  }
  
  .navbar-nav > li > a {
    color: #fff !important;
    color: var(--primary-white) !important;
    margin-left: .75rem;
    margin-right: .75rem;
    border-bottom: .1875rem solid rgba(225,225,225,0) !important;
    border-bottom: .1875rem solid var(--primary-dark) !important;
  }
  
   .navbar-nav>li>a.active, .navbar-nav > li> a:focus, .navbar-nav > li> a:hover{
    border-bottom: .1875rem solid rgb(116, 38, 38) !important;
    border-bottom: .1875rem solid var(--primary-red) !important;
  }
  
  /********************** header  ***************************/
  
  .header-wraper{
    position:relative;
    background: url(/static/media/bg.e223b8e4.jpg) no-repeat;
    background-size: 100vw 100vh;
    background-position:center ;
    height: 100vh;
  }
  .main-info{
    display: -webkit-flex;
    display: flex;
    position: absolute;
    top:0 ; left:0 ; right:0 ;bottom:0;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    z-index:1;
  }
  
  .typed-text{
    font-size: 2rem;
    color:rgb(116, 38, 38);
    color:var(--primary-red)
  }
  
  .tsparticles-canvas-el{
    position:absolute;
    height:100vh;
    width:100vw;
    z-index:1
  }
:root{
    --primary:#fff;
}
.btn{
    padding: 8px 20px;
    border-radius: 2px;
    border:none;
    outline:none;
    cursor:pointer
}
.btn--primary{
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    color: #242424;
    background-color:#fff;
    background-color:var(--primary);
}
.btn--outline{
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    color: #fff;
    color: var(--primary);
    background-color:transparent;
    transition: all 0.3s ease-out;
    margin-top: 20px;
}

.btn--medium{
    padding:8px 20px;
    font-size:20px
}
.btn--large{
    padding:12px 26px;
    font-size:20px;
}

.btn--medium:hover, .btn--large:hover{
    color: #242424;
    background-color:#fff;
    background-color:var(--primary);
    transition: all 0.3s ease-out;
}


